import React from 'react'
import './header.css'
import VideoPlayer from '../iframe/iframe'

function Header() {
  return (
    <header>
      <div className="container">
        <div className="header_display-flex">
            <div className="header_texts">
                <h1>Take your <br /> business to the next <br /> level take out</h1>
                <p className='header_p'>Nullam laoreet nec turpis et ultrices. Duis sit amet quam arcu. Nam facilisis lacinia ex, eget sollicitudin massa pellentesque in. Vivamus mattis eros at sem pulvinar</p>
                <button className="header_btn">Learn more</button>
            </div>
            <VideoPlayer />
        </div>
      </div>
    </header>
  )
}

export default Header
