import React from 'react'
import './section3.css'

function Section3() {
  return (
    <section className='section3'>
      <div className="container">
        <h4 className='section3_h3'>Leave your number and <br /> we'll call you back</h4>
        <p className='section3_p'>Nullam laoreet nec turpis et ultrices. Duis sit amet quam arcu. Nam facilisis <br /> lacinia ex, eget sollicitudin massa pellentesque in. Vivamus mattis eros at sem <br /> pulvinar</p>
      <div className="section3_bigDiv">
        <div className="sectipn3_inputs_div">
        <div className="section3_inputs">
            <input className='section3_inputs_input' type="text" placeholder='Name' />
            <input className='section3_inputs_input' type="number" placeholder='Phone number' />
            <select className='section3_select' id="select">
                <option className='section3_options' value="Description">Description</option>
                <option className='section3_options' value="Telegraph">Telegraph</option>
                <option className='section3_options' value="Instagram">Instagram</option>
                <option className='section3_options' value="Facebook">Facebook</option>
            </select>
            </div>
            <button className='section3_btn'>Send</button>
            <h6 className='section3_h6'>We call back in 10-30 minutes, guaranteed!</h6>
        </div>
      </div>
      </div>
    </section>
  )
}

export default Section3
